/* ChurnForm.module.css */
/* In index.css or App.css if you're using Create React App */
body, html {
  margin: 0;
  padding: 0;
  height: 100%;
}

.fullScreenContainer {
  margin: 0 !important;
  padding: 0 !important;
  min-height: 100vh;
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

h2 {
  text-align: center;
  margin: 0 0 30px 0;
  padding-bottom: 20px;
  font-size: 3rem;
  color: #480800;
}

.formContainer {
    max-width: 400px;
    margin: 0 auto 20px auto; /* No top margin */
    padding: 30px;
    background: #fe7070;
    border-radius: 12px;
    box-shadow: 0 8px 20px rgba(0,0,0,0.1);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* Keep all your other CSS rules but remove the .container prefix */

  .inputGroup {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 15px;

  }
  
  label {
    display: inline-block;
    width: 400px;       /* fixed label width (make it big enough for longest label) */
    text-align: left;  /* right align text so inputs start at same line */
    margin-right: 40px; /* gap between label and input */
    font-weight: 600;
    font-size: 1.2rem;
    color: #ffe0e0;
    white-space: nowrap; /* prevent label wrapping */
  }
  
  input[type="number"] {
    flex: 1;
    max-width: 80px;
    margin-bottom: 5px;
    padding: 8px 10px;
    font-size: 0.95rem;
    border: 2px solid #ffffff;
    box-shadow: #000000;
    border-radius: 5px;
    box-sizing: border-box;
  }
  
  input[type="number"]:focus {
    border-color: #ffaf77;
    outline: none;
    box-shadow: 0 0 5px #ff9c71;
  }
  
  button {
    width: 100%;
    max-width: 400px;
    margin-top: 25px;
    padding: 12px;
    background: #ffb700;
    border-width: 10px;
    border-radius: 2px;
    border-color: #ffcc00;
    color: rgb(0, 0, 0);
    font-size: 1.1rem;
    font-weight: 600;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background: #eb9f11;
  }
  
  .prediction {
    margin-top: 20px;
    padding: 12px;
    border-radius: 8px;
    background-color: #f0f4c3;
    color: #333;
    font-weight: bold;
    text-align: center;
    max-width: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  form {
    max-width: 400px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: stretch; /* stretch inputs full width inside form */
  }
  .popupOverlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.4); /* dim background */
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 999;
  }
  
  .popupContent {
    background-color: #ffe388;
    padding: 30px;
    border-radius: 12px;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);
    text-align: center;
    max-width: 500px;
    width: 90%;
    position: relative;
  }
  
  .closeButton {
    position: absolute;
    top: -25px;
    right: 50px;
    background: none;
    border: none;
    font-size: 1.5rem;
    font-weight: bold;
    cursor: pointer;
    color: #000000;
  }
  
  .predictionText {
    font-size: 1.2rem;
    font-weight: 600;
    margin-top: 15px;
  }
  
  /* Overlay background */
